export function renderProgressIndicator(currentStep) {
  const steps = [
    { id: 1, label: 'Triage' },
    { id: 2, label: 'Assessment' },
    { id: 3, label: 'Results' },
  ];

  let html = '<div class="flex items-center justify-between mb-6">';

  steps.forEach((step, index) => {
    const isActive = step.id === currentStep;
    const isCompleted = step.id < currentStep;

    // Step circle
    html += `
      <div class="flex-1 flex items-center">
        <div class="relative flex flex-col items-center text-center w-full">
          <div class="w-8 h-8 flex items-center justify-center rounded-full 
                      ${isCompleted ? 'bg-green-500 text-white' : isActive ? 'bg-blue-500 text-white' : 'bg-gray-300 text-gray-700'} 
                      font-bold">
            ${isCompleted ? '' : step.id}
          </div>
          <span class="mt-2 text-xs ${isActive ? 'text-blue-500' : 'text-gray-500'}">
            ${step.label}
          </span>
        </div>
        
        ${index < steps.length - 1 ? `<div class="flex-1 h-1 ${isCompleted ? 'bg-green-500' : 'bg-gray-300'}"></div>` : ''}
      </div>
    `;
  });

  html += '</div>';
  return html;
}
